.home{
    height: 100vh;
    width: 100%;
    background: black;
}

.semi-circle{
        height: 100vh;
        width: 48vh;
        background: rgba(255, 255, 255, 0.109);
        border-top-right-radius: 100vh;
        border-bottom-right-radius: 100vh;
        /* box-shadow: 0 0 25px white; */
        
        
    
}
.inner-semi-circle{
    height: 70vh;
    width: 35vh;
    background: rgba(255, 255, 255, 0.049);
   margin-top: 15vh;
   border-top-right-radius: 70vh;
   border-bottom-right-radius: 70vh;
    position: absolute;
    box-shadow: 0 0 25px black;
 
}
.second-inner-semi-circle{
    height: 50vh;
    width: 23vh;
    background: rgba(255, 255, 255, 0.049);
   margin-top: 10vh;
   border-top-right-radius: 50vh;
   border-bottom-right-radius: 50vh;
    position: absolute;
    box-shadow: 0 0 25px black;
}
.profile-image{
    height: 450px;
    width: auto;
    /* position: absolute; */
    /* margin-left: 55%; */
    /* opacity: .8; */
    border-radius: 450px;
    margin-top: 20vh;
    display: block;
    margin: auto;
    margin-top: 75px;
    /* background: red; */
    /* box-shadow: 0 0 25px pink; */
    box-shadow: 0 0 15px white;

}
.image-circle{
    height: 600px;
    width: 600px;
    /* background: rgb(58, 57, 57); */
    background:black;
    margin-left: 50%;
    position: absolute;
    margin-top: 15vh;
    border-radius: 600px;
    box-shadow: 0 0 25px white;
    
}
.profile-info{
    position: absolute;
    top: 60%;
    left: 20px;
    font-size: 60px;
    font-family: Arial, Helvetica, sans-serif;
    background: linear-gradient(to right, #f32170,  #ff6b08, #cf23cf, #eedd44);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    font-weight: bold;
    
}
.profile-info-one{
    position: absolute;
    top: 48%;
    left: 20px;
    font-size: 60px;
    font-family: Arial, Helvetica, sans-serif;
    background: linear-gradient(to right, #f32170,  #ff6b08, #cf23cf, #eedd44);
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    font-weight: bold;
}

